import React, { forwardRef } from 'react';
import { useMatchMedia } from '../../mq/modern';
import { PlateDesktop } from './desktop/Component.desktop.js';
import { PlateMobile } from './mobile/Component.mobile.js';
import './components/base-plate/Component.js';
import 'react-merge-refs';
import 'classnames';
import '../../icon-button/modern';
import '@alfalab/hooks';
import '@alfalab/icons-glyph/ChevronDownMIcon';
import '@alfalab/icons-glyph/CrossMIcon';
import './components/button-list/component.js';

const Plate = forwardRef(({ children, breakpoint = 1024, ...restProps }, ref) => {
    const query = `(min-width: ${breakpoint}px)`;
    const [isDesktop] = useMatchMedia(query);
    const Component = isDesktop ? PlateDesktop : PlateMobile;
    return (React.createElement(Component, { ref: ref, ...restProps }, children));
});

export { Plate };
