import React, { forwardRef } from 'react';
import { useMatchMedia } from '../../mq/modern';
import { TagDesktop } from './desktop/Component.desktop.js';
import { TagMobile } from './mobile/Component.mobile.js';
import './components/base-tag/Component.js';
import 'react-merge-refs';
import 'classnames';
import '@alfalab/hooks';

const Tag = forwardRef(({ children, breakpoint = 1024, ...restProps }, ref) => {
    const query = `(min-width: ${breakpoint}px)`;
    const [isDesktop] = useMatchMedia(query);
    const Component = isDesktop ? TagDesktop : TagMobile;
    return (React.createElement(Component, { ref: ref, ...restProps }, children));
});

export { Tag };
